import React, {
    useRef,
    useEffect,
    useState,
    forwardRef,
    useImperativeHandle
} from 'react';
import { MDCRadio } from '@material/radio';
import { FormField } from '@element/react-form-field';
import clsx from 'clsx';
import { InputLabel } from '@element/react-input-label';
import { edsRandomId } from '@element/react-common';
import { RadioPropTypes, RadioDefaultProps } from './generated/propTypes';

const Radio = forwardRef((props, externalRef) => {
    const {
        alignEnd,
        checked,
        children,
        className,
        disabled,
        hideLabel,
        id,
        label,
        name,
        onChange,
        themeColor,
        value,
        ...customProps
    } = props;

    const [idActual] = useState(() => id || edsRandomId());
    const radioRef = useRef();
    const mdcRadio = useRef();

    useImperativeHandle(externalRef, () => radioRef.current, []);

    const handleChange = event => {
        if (onChange) {
            onChange(event, value);
            return;
        }
        mdcRadio.current.checked = event.target.checked;
    };

    useEffect(() => {
        mdcRadio.current = MDCRadio.attachTo(radioRef.current);
        return () => {
            mdcRadio.current?.destroy();
        };
    }, [mdcRadio]);

    useEffect(() => {
        mdcRadio.current.value = value;
    }, [value]);

    useEffect(() => {
        mdcRadio.current.checked = checked;
    }, [checked]);

    useEffect(() => {
        mdcRadio.current.disabled = disabled;
    }, [disabled]);

    return (
        <FormField alignEnd={alignEnd} inputRef={mdcRadio}>
            <div
                ref={radioRef}
                className={clsx(
                    'lmnt mdc-radio lmnt-radio',
                    disabled && 'mdc-radio--disabled',
                    themeColor && `lmnt-radio--${themeColor}`,
                    className,
                    'mdc-touch-target-wrapper'
                )}
            >
                <input
                    id={idActual}
                    onChange={handleChange}
                    disabled={disabled}
                    name={name}
                    type='radio'
                    className='mdc-radio__native-control'
                    {...customProps}
                />
                <div className='mdc-radio__background'>
                    <div className='mdc-radio__outer-circle' />
                    <div className='mdc-radio__inner-circle' />
                </div>
                <div className='mdc-radio__ripple' />
            </div>
            <InputLabel
                hideLabel={hideLabel}
                targetId={idActual}
                className={clsx(disabled && 'lmnt-theme-on-surface-disabled')}
                disabled={disabled}
            >
                {label || children}
            </InputLabel>
        </FormField>
    );
});

Radio.displayName = 'Radio';
Radio.propTypes = RadioPropTypes;
Radio.defaultProps = RadioDefaultProps;

export default Radio;
